import axios from 'axios';


let base = '/oauth2-service';

export const requestLogin = params => {
  return axios.post(`${base}/login`, params);
};

export const logout = params => {
	return axios.get(`${base}/logout`, params);
}

export const getUserList = params => { return axios.get(`${base}/user/list`, { params: params }); };




export const getUserAccountListPage = params => { return axios.get(`${base}/useraccount/listpage`, { params: params }); };

export const removeUser = params => { return axios.get(`${base}/user/remove`, { params: params }); };

export const removeUserAccount = params => {
  return axios.post(`${base}/useraccount/delete`,params); };

export const batchRemoveUser = params => { return axios.post(`${base}/user/batchremove`, params); };

export const batchRemoveUserAccount = params => { return axios.post(`${base}/useraccount/deletebatch`, params ); };

export const editUser = params => { return axios.get(`${base}/user/edit`, { params: params }); };

export const editUserAccount = params => { return axios.post(`${base}/useraccount/update`,  params); };

export const addUser = params => { return axios.get(`${base}/user/add`, { params: params }); };

export const addUserAccount = params => {
   return axios.post(`${base}/useraccount/add`,params ); };
export const resetAuth = params => {
     return axios.post(`${base}/useraccount/resetAuth`,params );
   }



