import _ from 'underscore'


let filter = {
    install: function(Vue, options) {
        Vue.filter('myDate', function(input) {
            if (!input) {
                return input;
            } else {
                if (typeof input == "number") {
                    return new Date(input).format("yyyy-MM-dd hh:mm:ss")
                } else {
                    return input;
                }
            }
        });

        Vue.filter('json', function(input) {
            if (!input) {
                return input;
            } else {
                return JSON.stringify(input);
            }
        });
        Vue.filter('bool', function(input) {
            if (input) {
                return '是';
            } else {
                return '';
            }
        });
        Vue.filter('fieldName', function(input) {
            if (input) {
                var pre = "";
                for(var i = 0; i < input.length; i++){
                  if(input.charAt(i) == '.'){
                     pre += "  "
                  }
                }
                return pre + input;
            }
        });
        Vue.filter('myStatus', function(input) {
                if (input == "1") {
                    return "Active";
                } else {
                    return "InActive";
                }
        });
        Vue.filter('apiRequestStatus', function(input) {
            if (!input) {
                return input;
            } else {
                if (input == "1") {
                    return "草稿";
                } if(input == "2") {
                  return "待审核";
                }if(input == "3") {
                  return "审核通过";
                }if(input == "4") {
                  return "拒绝申请";
                }if(input == "5") {
                  return "退回修改";
                }
            }
        });
        Vue.filter('myUserType', function(input) {
            if (!input) {
                return input;
            } else {
                if (input == "1") {
                    return "user";
                }
                if (input == "2") {
                    return "client";
                }
            }
        });
    }
}


export default filter;



