import axios from 'axios';
let base = '/oauth2-service';

axios.defaults.headers.common['accessToken'] = sessionStorage.getItem('token');

export const getRoleUsers = params => {
    return axios.get(`${base}/roleuser/listbyaccountid`, {
        params: params
    });
};

export const addRoleUser = params => {
    return axios.post(`${base}/roleuser/add`, params);
};


export const removeRoleUser = params => {
    return axios.post(`${base}/roleuser/delete`, params);
};

export const resetRoleUser = params => {
    return axios.post(`${base}/roleuser/resetRoleUser`, params);
};



