import axios from 'axios';
let base = '/oauth2-service';


let api = {
  getRoles: function(params){
    return axios.get(`${base}/role/listpage`, { params: params });
  },
  removeRole: function(params){
    return axios.post(`${base}/role/delete`,params);
  },
  batchRemoveRole: function(params){
    return axios.post(`${base}/role/deletebatch`, params );
  },
  editRole: function(params){
     return axios.post(`${base}/role/update`,  params);
  },
  addRole: function(params){
    return axios.post(`${base}/role/add`,params );
  },
  resetAuth: function(params){
    return axios.post(`${base}/role/resetAuth`,params );
  }
}

export default api;



