import axios from 'axios';
let base = '/oauth2-service';

axios.defaults.headers.common['accessToken'] = sessionStorage.getItem('user');

export const addUserAccountUrl = params => {
   var userstr = sessionStorage.getItem('user');
   var user = JSON.parse(userstr)
   params["createBy"] = user.userId;
   params["updateBy"] = user.userId;

   return axios.post(`${base}/useraccounturl/add`,params );
 };


   export const removeUserAccountUrl = params => {
     return axios.post(`${base}/useraccounturl/delete`,params);
    };

   export const  listByClientId = params =>{

         return axios.get(`${base}/useraccounturl/listbyclientid`, { params: params });
       }



