import axios from 'axios';
import _ from 'underscore';

let base = '/apidoc/catalog';


let menu = {
	listpage : function(data){
		return axios.post(`${base}/listpage`, data);
	},
	listAll : function(){
		return axios.post(`${base}/listall`);
	},
	add: function(data){
		return axios.post(`${base}/add`, data);
	},
	update: function(data){
		return axios.post(`${base}/update`, data);
	},
	del: function(id){
		return axios.get(`${base}/delete?id=` + id);
	},
	switchOrder: function(one, other){
		var data = {
		   one : {
			  id : one.id,
			  sortNum: one.sortNum
		   },
		   other : {
			   id : other.id,
			   sortNum: other.sortNum
		   }
		}
		return axios.post(`${base}/switchOrder`, data);
	},
	fuzzyQuery: function(name){
		return axios.get(`${base}/fuzzyQuery?`, {params: {name: name}});
	},
	queryByIds: function(ids){
		 return  axios.post(`${base}/queryByIds`, ids);
	},
	getById: function(id){
		return axios.get(`${base}/get?id=` + id);
	},
	autoGenDoc: function(serviceId){
		return axios.get(`/apidoc/api/autoGenDoc?serviceId=` + serviceId);
	},
	clearDoc: function(serviceId){
		return axios.get(`/apidoc/api/clearDoc?serviceId=` + serviceId);
	}
}

export default menu;



