<template>
<div id="app">
    <section>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.userId" placeholder="用户名"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-select v-model="filters.status">
                        <el-option v-for="item in status" v-bind:data="item" v-bind:key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUserAccounts">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd">新增</el-button>
                </el-form-item>

            </el-form>
        </el-col>


        <!--列表-->
        <el-table :data="useraccounts" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <!-- <el-table-column prop="id" label="编号" width="100">
            </el-table-column> -->
            <el-table-column prop="userId" label="用户名" width="180" sortable>
            </el-table-column>
            <el-table-column prop="realName" label="姓名" width="120" sortable>
            </el-table-column>
            <el-table-column prop="mobileNo" label="手机号" width="140" sortable>
            </el-table-column>
            <el-table-column prop="status" label="状态" width="100">
                <template scope="scope">{{ scope.row.status | myStatus }}
</template>
				</el-table-column>
				<el-table-column prop="type" label="账户类型" min-width="120" sortable>
					<template scope="scope">
 {{ scope.row.type | myUserType }}
</template>

				</el-table-column>
				<el-table-column label="操作" width="300">
					<template scope="scope">
<el-button size="small" @click="handleEdit(scope.$index, scope.row)">
    编辑</el-button>
<el-button size="small" v-if="roleButtonVisible(scope.row.userId)" @click="roleAuthorize(scope.$index, scope.row)">角色授权</el-button>
<el-button size="small" v-if="urlButtonVisible(scope.row.userId)" @click="showAuth(scope.row, scope.$index)">接口授权</el-button>
<el-button type="danger" size="small" v-if="scope.row.status==1" @click="handleDel(scope.$index, scope.row)">删除</el-button>
</template>
				</el-table-column>
			</el-table>
			<el-row style="margin-top:10px;">
	        <el-col :span="24" style="text-align: right">
	            <el-pagination @size-change="sizeChange" @current-change="currentChange" :current-page="currentPage"
	                :page-sizes="[5,10, 20, 100, 200, 400]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
	                :total="total">
	            </el-pagination>
	        </el-col>
	    </el-row>



			<!--工具条-->


			<!--编辑界面-->
			<el-dialog title="编辑" v-model="editFormVisible" :close-on-click-modal="false">
				<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
					<el-form-item label="用户名" prop="userId">
						<el-input v-model="editForm.userId" disabled auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="姓名"  prop="realName">
	          <el-input v-model="editForm.realName" auto-complete="off"></el-input>
					</el-form-item>
					<el-form-item label="手机号" prop="mobileNo">
						<el-input v-model="editForm.mobileNo"  ></el-input>
					</el-form-item>
					<el-form-item label="状态" prop="status">
						<el-select  v-model = "editForm.status" >
		    			<el-option
		      		v-for="item in status"
							v-bind:data="item"
           		v-bind:key="item.value"
		      		:label="item.label"
		      		:value="item.value">
		    			</el-option>
		  			</el-select>
					</el-form-item>
					<el-form-item  label="账户类型" prop="type">
					<el-select disabled  v-model = "editForm.type" >
	    			<el-option
	      		v-for="item in usertype"
						v-bind:data="item"
						v-bind:key="item.value"
	      		:label="item.label"
	      		:value="item.value"
	      		:disabled="item.disabled">
	    			</el-option>
	  			</el-select>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click.native="editFormVisible = false">取消</el-button>
					<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
				</div>
			</el-dialog>

			<!--接口授权界面-->
			<el-dialog title="" v-model="authFormVisible" size="large" top="0px;" :close-on-click-modal="false">
				<el-row>
					<el-col :span="14">
						<el-row>
							<el-form  :inline="true" :model="urlForm" ref="urlQueryForm">
									<el-form-item label=" ">
								 <el-input v-model.trim="urlForm.url"  placeholder="接口地址"></el-input>
										</el-form-item>
								 <el-form-item>
										<el-button type="primary" @click="addUrl">增加</el-button>
										<el-button type="primary" @click="showSelectUrl">选择</el-button>
									</el-form-item>
							</el-form>

						</el-row>
						<el-row>
							<div style="height:350px; overflow:scroll;width:700px"  size="full">
								<ul>
								<li v-for="(u, index) in useraccounturls">{{u.url}}  <i class="el-icon-delete2" @click="removeRoleUrl(index)"></i></li>
								</ul>
							</div>
						</el-row>
					</el-col>
				</el-row>
				<div slot="footer" class="dialog-footer">
						<el-button @click="authFormVisible= false">取 消</el-button>
						<el-button type="primary" @click="saveAuth">确 定</el-button>
				</div>
			</el-dialog>

			<el-dialog title="" v-model="urlQueryVisable" top="20px" size="large" :close-on-click-modal="false">
        <el-form  :inline="true" :model="urlQueryForm" ref="urlQueryForm">
           <el-form-item label="分类:">
             <el-autocomplete v-model.trim="urlQueryForm.catalog" :fetch-suggestions="querycatalog" placeholder="接口分类" @select="selectQueryCatalog"></el-autocomplete>
           </el-form-item>
            <el-form-item label="接口:">
           <el-input v-model.trim="urlQueryForm.url"  placeholder="接口"></el-input>
              </el-form-item>
           <el-form-item>
              <el-button type="primary" @click="querySelectUrl">查询</el-button>
            </el-form-item>
        </el-form>
          <div style="height:200px; overflow:scroll; overflow-x: hidden;">
            <el-table :data="urlQueryForm.selectUrlList" border style="width: 100%" @selection-change="selectUrl">
               <el-table-column type="selection" width="55">
                </el-table-column>
                <el-table-column prop="catalog" label="分类" width="180">
                </el-table-column>
                <el-table-column prop="name" label="名称"  width="250">
                </el-table-column>
                <el-table-column prop="url" label="地址">
                </el-table-column>
           </el-table>
        </div>
        <div slot="footer" class="dialog-footer">
						<el-button @click="urlQueryVisable= false">取 消</el-button>
						<el-button type="primary" @click="addSelectUrl" >确 定</el-button>
				</div>
			</el-dialog>

			<!--角色授权界面-->
			<el-dialog title="角色授权" v-model="roleAuthorizeFormVisible" top="10px" :close-on-click-modal="false">
				<el-form :model="roleAuthorizeForm" label-width="80px"  ref="roleAuthorizeForm">
					<div style="height:280px; overflow:scroll; overflow-x: hidden;">
							<el-tree :data="roles" :default-expand-all=true :expand-on-click-node=true :show-checkbox=true node-key="id" :check-strictly=true
											 :default-checked-keys="this.user_roleids"
									ref="roleTree" highlight-current :props="roleDefaultProps">
							</el-tree>
					</div>
					<el-row style="margin-top:10px;">
						<el-button type="primary" @click="saveRoleList" >确 定</el-button>
						<el-button @click="roleAuthorizeFormVisible= false">取 消</el-button>
					</el-row>
				</el-form>
			</el-dialog>


			<!--新增界面-->
			<el-dialog title="新增" v-model="addFormVisible" :close-on-click-modal="false">
				<el-form :model="addForm" label-width="100px" :rules="addFormRules" ref="addForm">
					<el-form-item label="登录名" prop="userId">
						<el-input v-model="addForm.userId" auto-complete="off"></el-input>
					</el-form-item>
	        <el-form-item label="密码" prop="password">
	          <el-input type="password" v-model="addForm.password" auto-complete="off"></el-input>
	        </el-form-item>
	        <el-form-item label="姓名"  prop="realName">
	          <el-input v-model="addForm.realName" auto-complete="off"></el-input>
					</el-form-item>
	        <el-form-item label="手机号"  prop="mobileNo">
						<el-input v-model="addForm.mobileNo"  ></el-input>
					</el-form-item>
					</el-form-item>
					<el-form-item label="账户类型" prop="type">
					<el-select  v-model = "addForm.type" placeholder="请选择"  >
	    			<el-option
	      		v-for="item in usertype"
						v-bind:data="item"
						v-bind:key="item.value"
	      		:label="item.label"
	      		:value="item.value"
	      		>
	    			</el-option>
	  			</el-select>
					</el-form-item>
				</el-form>
				<div slot="footer" class="dialog-footer">
					<el-button @click.native="addFormVisible = false">取消</el-button>
					<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
				</div>
			</el-dialog>
		</section>
	</div>
</template>

<script>
import NProgress from 'nprogress'
import {
    getUserAccountListPage,
    removeUserAccount,
    batchRemoveUserAccount,
    editUserAccount,
    addUserAccount,
    resetAuth
} from 'components/api/api';
import role from 'components/api/role.js';
import {
    getRoleUsers,
    addRoleUser,
    removeRoleUser,
    resetRoleUser
} from 'components/api/roleuser.js';
import filter from 'components/common/filter';
import roleUrl from 'components/api/auth/roleurl.js';
import {
    addUserAccountUrl,
    removeUserAccountUrl,
    listByClientId
} from 'components/api/auth/useraccounturl';
import catalog from 'components/api/auth/apicatalog';
import apidoc from 'components/api/auth/apidoc';

import {
    listpage,
    list
} from 'components/api/auth/apidoc';
import _ from 'underscore';

export default {
    data() {
				function checkMobile(rule, value, callback) {

            if (value &&
                !(/^1[3|4|5|7|8][0-9]\d{4,8}$/.test(value))) {
                console.log("execute here");
                callback(new Error('请输入正确格式的手机号!'))
            }else {
              callback();
            }
        }
        return {
            currentPage: 1,
            pageSize: 10,
            usertype: [{
                    value: "1",
                    label: "user"
                },
                {
                    value: "2",
                    label: "client"
                }
            ],
            status: [{
                    value: 1,
                    label: "Active"
                },
                {
                    value: 0,
                    label: "InActive"
                }
            ],
            filters: {
                userId: '',
                status: 1
            },
            urlSetCheckedKeys: false,
            roleSetCheckedKeys: false,
            useraccounts: [],
            roles: [],
            urls: [],
            useraccounturls: [],
            user_roleids: [],
            useraccount_urlids: [],
            urlQueryVisable: false,
            urlSeleteItems: null,
            editingClientId: null,
            roleDefaultProps: {
                label: 'roleName'
            },
            urlDefaultProps: {
                children: 'children',
                label: 'name'
            },
            total: 0,
            listLoading: false,
            sels: [], //列表选中列
            authFormVisible: false,
            urlAuthorizeFormVisible: false,
            roleAuthorizeFormVisible: false,

            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                name: [{
                    required: true,
                    message: '请输入姓名',
                    trigger: 'blur'
                }, ],
                mobileNo: [{
                    validator: checkMobile,
                    trigger: 'blur'
                }]
            },
            //编辑界面数据
            editForm: {
                id: 0,
                userId: '',
                realName: '',
                mobileNo: '',
                status: '',
                type: ''
            },
            //编辑界面数据
            urlAuthorizeForm: {
                id: 0,
                userId: '',
                realName: '',
                mobileNo: '',
                status: '',
                type: ''
            },
            roleAuthorizeForm: {
                id: 0,
                userId: '',
                realName: '',
                mobileNo: '',
                status: '',
                type: ''
            },

            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                userId: [{
                    required: true,
                    message: '请输入用户名',
                    trigger: 'blur'
                }],
                password: [{
                    required: true,
                    message: '请输入用户密码',
                    trigger: 'blur'
                }],
                type: [{
                    required: true,
                    message: '请选择用户类型',
                    trigger: 'change'
                }],
                mobileNo: [{
                    validator: checkMobile,
                    trigger: 'blur'
                }]
            },
            //新增界面数据
            addForm: {
                id: 0,
                userId: '',
                password: '',
                realName: '',
                mobileNo: '',
                type: ''
            },
            urlQueryForm: {
                catalog: '',
                catalogId: '',
                url: '',
                selectUrlList: []
            },
            urlForm: {
                url: ''
            }

        }
    },
    methods: {
        showAuth(row, index) {
            var userAccountId = row.id;
            this.editingClientId = userAccountId;
            let para = {
                userAccountId: userAccountId
            }
            var _this = this;
            listByClientId(para).then((res) => {
                console.info(res);
                var useraccounturls = res.data;
                _this.useraccounturls = useraccounturls;
                _this.authFormVisible = true;
            })
        },
        saveAuth() {
            var _this = this;
            var urls = this.useraccounturls;
            resetAuth({
                urls: urls,
                userAccountId: _this.editingClientId
            }).then(function () {
                _this.$notify.success({
                    message: '接口授权成功!',
                    duration: 1000
                });
                _this.authFormVisible = false;
            })
        },
        querycatalog(name, cb) {
            if (!name) {
                name = '';
            }
            catalog.fuzzyQuery(name).then(resp => {
                var result = [];
                _.each(resp.data, function (d) {
                    result.push({
                        value: d.name,
                        id: d.id
                    })
                })
                cb(result);
            })
        },
        querySelectUrl() {
            var _this = this;
            var para = {
                catalogId: _this.urlQueryForm.catalogId,
                url: _this.urlQueryForm.url
            }
            apidoc.fuzzyQuery(para).then(resp => {
                _this.urlQueryForm.selectUrlList = resp.data;
            })
        },
        addUrl: function () {
            var _this = this;
            var url = _this.urlForm.url;
            if (!url) {
                _this.$message.error({
                    showClose: true,
                    message: '请输入接口地址'
                });
            } else {
                _this.addRoleUrl({
                    url: url
                });
                _this.urlForm.url = null;
            }
        },
        addRoleUrl(item) {
            var _this = this;
            if (item && item.url) {
                var index = _.findLastIndex(_this.useraccounturls, {
                    url: item.url
                });
                if (index == -1) {
                    _this.useraccounturls.push({
                        url: item.url,
                        urlId: item.id
                    });
                } else {
                    _this.$message.error({
                        showClose: true,
                        message: '接口地址已存在，请重新输入'
                    });

                }
            }
        },
        removeRoleUrl(index) {
            this.useraccounturls.splice(index, 1);
        },
        selectUrl(items) {
            this.urlSeleteItems = items;
        },
        addSelectUrl() {
            var _this = this;
            _.each(_this.urlSeleteItems, function (u) {
                _this.addRoleUrl(u);
            })
            _this.urlQueryVisable = false;
        },
        selectQueryCatalog(item) {
            this.urlQueryForm.catalogId = item.id;
        },
        selectQueryUrl(item, cb) {
            this.useraccounturls.push({
                urlId: item.id,
                url: item.url
            });
            this.urlQueryForm.url = '';
        },
        showSelectUrl() {
						this.urlQueryForm.selectUrlList = [];
            this.urlQueryVisable = true;
            this.$refs.urlQueryForm.resetForm();
        },
        startRoleAuthorize() {
            this.roleSetCheckedKeys = false;
        },
        stopRoleAuthorize() {
            this.roleSetCheckedKeys = true;
        },
        startUrlAuthorize() {
            this.urlSetCheckedKeys = false;
        },
        stopUrlAuthorize() {
            this.urlSetCheckedKeys = true;
        },
        sizeChange(val) {
            this.pageSize = val;
            this.getUserAccounts();
        },
        currentChange(val) {
            this.currentPage = val;
            this.getUserAccounts();
        },
        urlAuthorize: function (index, row) {
            this.getUserAccountUrls(row.id);
            this.urlAuthorizeForm = Object.assign({}, row);
        },
        roleAuthorize: function (index, row) {
            this.getUserAccountRoles(row.id);
            this.roleAuthorizeForm = Object.assign({}, row);
        },
        //通过checkbox进行url授权
        urlHandleCheckChange(data, checked, indeterminate) {

            if (checked == true) {
                //添加roleme记录
                let para = {
                    userAccountId: this.urlAuthorizeForm.id,
                    urlId: data.id,
                    url: data.url,
                    systemId: 1
                }
                addUserAccountUrl(para).then((res) => {
                    if (this.urlSetCheckedKeys == false) {
                        this.$notify({
                            title: '成功',
                            message: '接口授权成功',
                            type: 'success',
                            duration: 1000
                        });
                    }
                });
            } else {
                //删除roleuser记录
                let para = {
                    userAccountId: this.urlAuthorizeForm.id,
                    urlId: data.id
                }
                if (this.urlSetCheckedKeys == false) {
                    removeUserAccountUrl(para).then((res) => {
                        this.$notify({
                            title: '成功',
                            message: '撤销接口授权成功',
                            type: 'success',
                            duration: 1000
                        });
                    });
                }
            }


        },
        //通过checkbox进行角色授权
        roleHandleCheckChange(data, checked, indeterminate) {

            if (checked == true) {
                //删除roleuser记录
                let para = {
                    userAccountId: this.roleAuthorizeForm.id,
                    roleId: data.id,
                    systemId: 1
                }
                if (this.roleSetCheckedKeys == false) {
                    addRoleUser(para).then((res) => {
                        this.$notify({
                            title: '成功',
                            message: '角色授权成功',
                            type: 'success',
                            duration: 1000
                        });
                    });
                }
            } else {
                //添加roleuser记录
                let para = {
                    userAccountId: this.roleAuthorizeForm.id,
                    roleId: data.id
                }
                if (this.roleSetCheckedKeys == false) {
                    removeRoleUser(para).then((res) => {
                        this.$notify({
                            title: '成功',
                            message: '撤销角色授权成功',
                            type: 'success',
                            duration: 1000
                        });
                    });
                }
            }


        },

        roleButtonVisible(userId) {
            var users = this.useraccounts;
            for (var i = 0; i < users.length; i++) {
                if (userId == users[i].userId) {
                    if (users[i].type == "1" && users[i].status == "1")
                        return true;
                }
            }
        },
        urlButtonVisible(userId) {
            var users = this.useraccounts;
            for (var i = 0; i < users.length; i++) {
                if (userId == users[i].userId) {
                    if (users[i].type == "2" && users[i].status == "1")
                        return true;
                }
            }
        },
        loadNode(node, resolve) {
            if (node.level === 0) {
                return resolve(this.roles);
            }



        },

        //获取角色列表
        getRoles() {
            let para = {
                pageNumber: 1,
                listAccount: 10000,
                userAccountId: this.useraccounts.id
            }
            role.getRoles(para).then((res) => {
                this.roles = res.data.records;
            });
        },
        //获取用户列表
        getUserAccounts() {
            let para = {
                pageNumber: this.currentPage,
                listAccount: this.pageSize
            };

            var userIdParam = this.filters.userId;

            if (userIdParam != '' & userIdParam != "" & userIdParam != "undefined" & userIdParam != null) {
                para["userId"] = userIdParam;
            }
            var status = this.filters.status;

            para["status"] = status;


            this.listLoading = true;
            NProgress.start();
            getUserAccountListPage(para).then((res) => {
                this.total = res.data.total;
                this.useraccounts = res.data.records;
                this.listLoading = false;
                NProgress.done();
            });
        },
        getUserAccountUrls(userAccountId) {
            let para = {
                userAccountId: userAccountId
            }
            listByClientId(para).then((res) => {
                var useraccounturls = res.data;
                this.useraccount_urlids = [];
                for (var i = 0; i < useraccounturls.length; i++) {
                    this.useraccount_urlids.push(useraccounturls[i].urlId);
                }
                this.urlAuthorizeFormVisible = true;
                this.urlSetCheckedKeys = true;
                this.$refs.urlTree.setCheckedKeys(this.role_urlids);
            })
        },
        getUserAccountRoles(userAccountId) {
            let para = {
                userAccountId: userAccountId
            }
            getRoleUsers(para).then((res) => {
                var roleusers = res.data;
                this.user_roleids = [];
                for (var i = 0; i < roleusers.length; i++) {
                    this.user_roleids.push(roleusers[i].roleId);
                }
                this.roleAuthorizeFormVisible = true;
                this.roleSetCheckedKeys = true;
                this.$refs.roleTree.setCheckedKeys(this.user_roleids);
            })
        },

        //删除
        handleDel: function (index, row) {
            this.$confirm('确认删除该记录吗?', '提示', {
                type: 'warning'
            }).then(() => {
                this.listLoading = true;
                NProgress.start();
                let para = {
                    userId: row.userId,
                    type: row.type
                };
                removeUserAccount(para).then((res) => {
                    this.listLoading = false;
                    NProgress.done();
                    this.$notify({
                        title: '成功',
                        message: '删除成功',
                        type: 'success',
                        duration: 1000
                    });
                    this.getUserAccounts();
                });
            }).catch(() => {

            });
        },
        //显示编辑界面
        handleEdit: function (index, row) {
            this.editFormVisible = true;
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd: function () {
            this.addFormVisible = true;
            this.addForm = {
                id: 0,
                userId: '',
                password: '',
                realName: '',
                mobileNo: '',
                type: ''
            };
        },
        //编辑
        editSubmit: function () {
            var _this = this;
            console.log("editSubmit");
            this.$refs.editForm.validate((valid) => {
              console.log("valid:"+valid);
                if (valid) {
                  this.editLoading = true;
                  NProgress.start();
                  let para = Object.assign({}, this.editForm);
                  editUserAccount(para).then((res) => {
                      this.editLoading = false;
                      NProgress.done();
                      this.$notify({
                          title: '成功',
                          message: '提交成功',
                          type: 'success',
                          duration: 1000
                      });
                      this.$refs['editForm'].resetFields();
                      this.editFormVisible = false;
                      this.getUserAccounts();
                  }).catch(function(err){
                     console.error(err);
                    _this.editLoading = false;
                  });
                }
            });
        },
        //保存权限列表
        saveRoleList: function () {
            var _this = this;
            var data = {
                roleIds: _this.$refs.roleTree.getCheckedKeys(),
                userId: _this.roleAuthorizeForm.id
            }
            resetRoleUser(data).then(function (resp) {
                _this.$notify.success({
                    message: '保存成功',
                    duration: 1000
                });
                _this.roleAuthorizeFormVisible = false;
            })

        },
        //新增
        addSubmit: function () {
            var _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {

                      this.addLoading = true;
                      NProgress.start();
                      let para = Object.assign({}, this.addForm);
                      para['status'] = 1;
                      addUserAccount(para).then((res) => {
                          this.addLoading = false;
                          NProgress.done();
                          this.$notify({
                              title: '成功',
                              message: '提交成功',
                              type: 'success',
                              duration: 1000
                          });
                          this.$refs['addForm'].resetFields();
                          this.addFormVisible = false;
                          this.getUserAccounts();
                      }).catch(function(err){
                         console.error(err);
                        _this.addLoading = false;
                      });;
                }
            });
        },
        selsChange: function (sels) {
            this.sels = sels;
        },
        //批量删除
        batchRemove: function () {
            var ids = this.sels.map(item => item.id).toString();
            this.$confirm('确认删除选中记录吗？', '提示', {
                type: 'warning'
            }).then(() => {
                this.listLoading = true;
                NProgress.start();
                let para = {
                    userAccountIds: ids
                };
                batchRemoveUserAccount(para).then((res) => {
                    this.listLoading = false;
                    NProgress.done();
                    this.$notify({
                        title: '成功',
                        message: '删除成功',
                        type: 'success',
                        duration: 1000
                    });
                    this.getUserAccounts();
                });
            }).catch(() => {

            });
        }
    },
    mounted() {
        this.getUserAccounts();
        this.getRoles();
    }
}
</script>

