import axios from 'axios';
let base = '/oauth2-service';


let api = {
    addRoleUrl: function(params) {
        var userstr = sessionStorage.getItem('user');
        var user = JSON.parse(userstr)
        params["createBy"] = user.userId;
        params["updateBy"] = user.userId;

        return axios.post(`${base}/roleurl/add`, params);
    },
    removeRoleUrl: function(params) {
        return axios.post(`${base}/roleurl/delete`, params);
    },
    listRoleUrl: function(params) {
        return axios.get(`${base}/roleurl/listbyroleid`, {
            params: params
        });
    }
}

export default api;



